export default {
	name: "hxNumberBox",
	props: {
		value: {
			type: [Number, String],
			default: 0
		},
		min: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 100
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		},
		rowData: {
			type: Object,
			default: ()=>{
				return {}
			}
		},
		clickTime: {
			type: Number,
			default: 0
		}
		
	},
	data() {
		return {
			inputValue: 0,
			addStaus: true,
		};
	},
	watch: {
		value(val) {
			this.inputValue = +val;
		},
		inputValue(newVal, oldVal) {
			if (+newVal !== +oldVal) {
				//this.$emit("change", newVal,this.rowData);
			}
			/* if(+newVal > +oldVal){
				
			} */
		}
	},
	created() {
		this.inputValue = +this.value;
	},
	methods: {
		_calcValue(type) {
			let that = this;
			if (this.disabled) {
				return;
			}
		
			const scale = this._getDecimalScale();
			let value = this.inputValue * scale;
			let step = this.step * scale;
			
			
			if (type === "minus") {
				this.$emit("lessChange", this.inputValue,this.rowData);
				value -= step;
				if (value < this.min) {
					return;
				}
				if(value > this.max){
					value = this.max
				}
			} else if (type === "plus") {
				this.$emit("addChange", this.inputValue,this.rowData);
				if(that.clickTime > 0){
					if(!this.addStaus){
						return;
					}else{
						this.addStaus = false;
						setTimeout(()=>{
							that.addStaus = true;
						},that.clickTime)
					}
				}
				value += step;
				if (value > this.max) {
					return;
				}
				if(value < this.min){
					value = this.min
				}
			}

			this.inputValue = String(value / scale);
			this.$emit("change", this.inputValue,this.rowData);
		},
		_getDecimalScale() {
			let scale = 1;
			// 浮点型
			if (~~this.step !== this.step) {
				scale = Math.pow(10, (this.step + "").split(".")[1].length);
			}
			return scale;
		},
		_onBlur(event) {
			let value = event.detail.value;
			if (!value) {
				// this.inputValue = 0;
				return;
			}
			value = +value;
			if (value > this.max) {
				value = this.max;
			} else if (value < this.min) {
				value = this.min;
			}
			this.inputValue = value;
		}
	}
};
